.Entete {
    position: sticky;
    top:0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #444;
    color: white;

    h1 {
        font-size: 1.4rem;
        padding: 1rem 2rem;
    }

    nav{
        padding: 1rem 2rem;
        display: flex;
        align-items: center;
        a{
            padding-left: 0.7rem;
            font-size: 0.9rem;
            white-space: nowrap;
        }
    }
}